import ClickOutside from 'vue-click-outside';
import Scrollbar from '@/components/Components/Scrollbar/index.vue';

export default {
  name: 'Sticker',
  props: ['action'],
  data() {
    return {
      isShowBox: false,
      stickers: Array.from({ length: 40 }).map(
        (k, i) => `FunFunFamily-${i + 1}`
      )
    };
  },
  components: {
    Scrollbar
  },
  directives: {
    ClickOutside
  },
  computed: {},
  methods: {
    open() {
      this.isShowBox = !this.isShowBox;
    },
    close() {
      if (!this.isShowBox) {
        return;
      }
      this.isShowBox = false;
      this.$emit('closeActionsGroup');
    },
    send(sticker) {
      let msg = {
        messageType: 'sticker',
        sticker: sticker
      };
      this.$emit('outputScrollToBottom');
      this.$emit('sendSticker', msg);
      this.close();
    }
  }
};



